@import url(https://fonts.googleapis.com/css?family=Roboto:500);

h1 {
  text-align: center;
}

.App {}

.search-bar {
  display: flex;
  justify-content: center;
}

.CitySearch {
  padding: 5px;
}

.numberOfEvents {
  padding: 5px;
}

ul {
  padding: 0;
  list-style: none;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.Alert {
  height: 4em;
  width: 20vw;
  font-size: smaller;
}

input[type="text"],
input[type="number"] {
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  align-self: center;
  height: 38px;
}

.suggestions {
  
  z-index: 2;
  position: absolute;
   align-self: center; 
  margin: 0;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75); 
 } 

 .suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}






